@import './common.scss';

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 60px 0;
    
    .logo{
        .logo_title {
            font-family: 'Georgia';
            font-style: normal;
            font-weight: normal;
            font-size: 32px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: $primary;
            margin-bottom: 10px;
            display: block;
        }
        
        .logo_subtitle {
            font-family: 'Arial';
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.1em;
            color: $white;
            text-align: center;
        }
     
        
    }
    .burger {
        display: none;
        
    }
    .nav {
        display: flex;
        padding-bottom: 2px;
        list-style: none;

        
        .nav_item {
            margin-left: 35px;
            margin-right: 1px;
            
        }
        
        .nav_link {
            font-family: 'Arial';
            font-size: 15px;
            line-height: 160%;
            color: $light_grey_main-screen;
            text-decoration: none;

            display: block;
            
            &:hover {
                color: $non_active_card_bg;
            }
            
            &.nav_link--active {
                color: $non_active_card_bg;
    
                border-bottom: 3px solid $primary;
            }
        }
        
    }
    
    .nav-mobile-wrapper {
        position: fixed;
        top: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        transition: right 0.3s ease-in-out;
        right: -100%;
        
        
        .nav-mobile {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            right: 0;
            padding: 0 38px;
            width: 320px;
        height: 100%;
        background: #292929;
        box-sizing: border-box;
            
            .nav_item-mobile {
                font-size: 32px;
                line-height: 160%;
                margin-bottom: 40px;
                text-align: center;
                
                
                .nav_link{
                    color: #CDCDCD;
                    text-decoration: none;
                    
                    &.nav_link--active {
                        color: #FAFAFA;
                        border-bottom: 3px solid #F1CDB3;
                    }
                    &.nav_link--inactive:hover {
                        color: #CDCDCD;
                        cursor: not-allowed;
                    }
                    &:hover{
                        color: #FAFAFA;
                    }
                }
                
            }
            
        }
        
        
        
        &.--active {
            right: 0;
            transition: right 0.3s ease-in-out;
        }
        
        
    }
    
    .nav-mobile-wrapper::before {
        content: "";
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.7);
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
        visibility: hidden;
        opacity: 0;
    }
    
        .nav-mobile-wrapper.--active::before {
        opacity: 1;
        visibility: visible;
    }
    
        
}

body.--blocked {
    overflow: hidden;
    background: rgba(41, 41, 41, 0.6);
}
@media (max-width: 767px) {
    header .nav {
        display: none;
    }
    
    header .burger {
        display: flex;
        align-items: center;
        margin-right: 43px;
        z-index: 1;
        transition: transform 0.3s ease-in-out;
        
        &.--active {
            transform: rotate(90deg);
            transition: transform 0.3s ease-in-out;
        }
    }
    header .burger:hover {
        cursor: pointer;
    }
    
    
}
@media (min-width: 768px){
    .nav-mobile-wrapper {
        display: none;
    }
}